.feedback__checkbox label {
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
}

.feedback__checkbox-mark {
  position: relative;
  margin: 0;
  margin-right: 10px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.7);
  border-radius: 2px;
  outline: none;
}
.feedback__checkbox-mark:hover {
  border-color: #ffffff;
}

.feedback__checkbox input:hover + .feedback__checkbox-mark,
.feedback__checkbox input:focus-visible + .feedback__checkbox-mark {
  background-color: #316dc2;
  background-image: url("../img/sprite.svg#checkbox-white");
}

.feedback__checkbox input:checked + .feedback__checkbox-mark {
  background: #ffffff url("../img/sprite.svg#checkbox") no-repeat center/14px 8px;
}
.feedback__checkbox input:checked + .feedback__checkbox-mark:hover, .feedback__checkbox input:checked + .feedback__checkbox-mark:focus-visible {
  background-color: #316dc2;
  background-image: url("../img/sprite.svg#checkbox-white");
}

.feedback__checkbox input:checked:disabled + .feedback__checkbox-mark {
  background: #ffffff url("../img/sprite.svg#checkbox-white") no-repeat center/14px 8px;
  background-color: #a9afb7;
  border: none;
}

.feedback__checkbox input:disabled + .feedback__checkbox-mark {
  background-color: rgba(255, 255, 255, 0.6);
  pointer-events: none;
}

.feedback__checkbox.is-invalid .feedback__checkbox-mark {
  border: 1px solid #ff5e66;
  opacity: 0.7;
  background: rgba(255, 94, 102, 0.2);
}

.feedback__btn .feedback__icon-wrapper {
